@import "../../global.scss";

.portfolio {
  background-color: #d6d8d9;
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow: hidden;


  h1 {
    height: 10vh;
    font-size: 6vh;
    display: flex;

  }



  .menuBar {

    height: auto;
    width: auto;
    display: flow;
    overflow: hidden;

    ul {
      margin: 10px;
      padding: 0;
      list-style: none;
      display: flex;
      height: inherit;


      li {
        margin: 20px;
        font-size: 5vh;
        display: flow;


      }

    }
  }

  .skillsList {
    background-color: $mainColor;
    color: white;
    height: auto;
    width: auto;
    border-radius: 10px;
    padding: 20px 40px;
    display: table;

    li{
      font-size: 4vh;
    }

  }

  .container {
    width: 70%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;


    .item {
      width: 220px;
      height: 150px;
      border-radius: 20px;
      border: 1px solid rgb(240, 239, 239);
      margin: 10px 20px;
      display: flex;
      align-items: center;
      justify-content: center;
      color: white;
      position: relative;
      transition: all .5s ease;
      cursor: pointer;


      h3 {
        position: absolute;
        font-size: 20px;
      }

      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        z-index: 1;
      }

      &:hover {
        background-color: $mainColor;
        img {
          opacity: 0.2;
          z-index: 0;
        }
      }
    }
  }

  .icon {
    text-align: center;
    color: crimson;
    display: flex;
    height:auto;
    width: auto;

    .arrow {
      color: $mainColor;
      font-size: 15vh;

      &:hover{
        color: crimson;
        transition: all .4s ease;
      }
    }

  }
}